@font-face {
  font-family: "MasvisHC";
  src: url(../../../Masvis.ttf);
}
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "MasvisHC", sans-serif;
  font-size: 1.6rem;
}

img {
  width: 100%;
  display: block;
}

picture {
  all: unset;
  width: auto;
  height: auto;
  display: inline-block;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

h1,
h2,
h3,
.header__texto {
  color: #A9143B;
}

[class$=__contenedor] {
  max-width: 120rem;
  margin: 0 auto;
}

[class$=__capa] {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(247, 246, 242, 0.4);
  mix-blend-mode: overlay;
}

.header {
  min-height: 100vh;
  position: relative;
}

.header__videoFondo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header__contenido {
  position: relative;
  width: 100%;
  z-index: 2;
  display: flex;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.header__contenido .header__heading {
  font-size: 5rem;
  font-weight: 700;
  margin: 0;
  line-height: 0.8;
  transition: all 0.4s ease-in;
}
.header__contenido .header__heading:hover {
  font-size: 7rem;
  padding: 1rem 0;
  border-radius: 10px;
  box-shadow: 0 0 15px 12px rgba(247, 246, 242, 0.5);
  background: rgba(247, 246, 242, 0.5);
}
.header__contenido .header__heading2 {
  margin: 0;
  font-size: 2rem;
  line-height: 0;
  letter-spacing: 1px;
  font-weight: 300;
}
.header__contenido .header__texto {
  max-width: 60%;
  margin-top: 5rem;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 700;
  letter-spacing: 2px;
}
.header__contenido .header__heading3 {
  margin-top: 3rem;
  letter-spacing: 2px;
}
@media (min-width: 768px) {
  .header__contenido .header__heading {
    font-size: 15rem;
    font-weight: 700;
    margin: 0;
    line-height: 0.8;
    transition: all 0.4s ease-in;
  }
  .header__contenido .header__heading:hover {
    font-size: 17rem;
    padding: 2rem 0;
    border-radius: 10px;
    box-shadow: 0 0 15px 12px rgba(247, 246, 242, 0.5);
    background: rgba(247, 246, 242, 0.5);
  }
  .header__contenido .header__heading2 {
    margin: 0;
    line-height: 0;
    letter-spacing: 2.2rem;
    font-weight: 300;
  }
  .header__contenido .header__texto {
    max-width: 60%;
    margin-top: 5rem;
    text-align: center;
    font-size: 2rem;
    font-weight: 700;
    letter-spacing: 2px;
  }
}

.header__barra {
  position: relative;
  width: 100%;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: 768px) {
  .header__barra {
    position: relative;
    width: 100%;
    z-index: 2;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
  }
}
.header__logo {
  max-width: 15rem;
  max-height: 15rem;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .header__logo {
    max-width: 30rem;
    max-height: 30rem;
  }
}
.header__logoImagen {
  width: 100%;
  height: 100%;
}

.nav {
  margin: 0 auto 7rem auto;
}

.nav__lista {
  height: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  padding: 0;
  margin-top: 9rem;
}
.nav__lista .nav__link {
  color: #F7F5DD;
  margin: o auto;
}

@media (min-width: 768px) {
  .nav__lista {
    height: 10rem;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-around;
    align-items: center;
    gap: 2rem;
  }
}