@use '../base/variables'as v;
@use '../base/mixins' as m;

.header {
    min-height: 100vh;
    position: relative;
}

.header__videoFondo {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.header__contenido {
    position: relative;
    width: 100%;
    z-index: 2;
    display: flex;
    display: flex;
    flex-direction: column;
    align-items: center;

    .header__heading {
        font-size: 5rem;
        font-weight: 700;
        margin: 0;
        line-height: .8;
        transition: all .4s ease-in;
    }
    
    .header__heading:hover {
        font-size: 7rem;
        padding: 1rem 0;
        border-radius: 10px;
        box-shadow: 0 0 15px 12px rgb(247 246 242 /.5 );
        background: rgb(247 246 242 /.5 );
    
    }
    
    .header__heading2 {
        margin: 0;
        font-size: 2rem;
        line-height: 0;
        letter-spacing: 1px;
        font-weight: 300;
    }
    
    .header__texto {
        max-width: 60%;
        margin-top: 5rem;
        text-align: center;
        font-size: 1.5rem;
        font-weight: 700;
        letter-spacing: 2px;
    }
    
    .header__heading3 {
        margin-top: 3rem;
        letter-spacing: 2px;
    }

    @include m.mq(768px){
        .header__heading {
            font-size: 15rem;
            font-weight: 700;
            margin: 0;
            line-height: .8;
            transition: all .4s ease-in;
        }
        .header__heading:hover {
            font-size: 17rem;
            padding: 2rem 0;
            border-radius: 10px;
            box-shadow: 0 0 15px 12px rgb(247 246 242 /.5 );
            background: rgb(247 246 242 /.5 );
        
        }
        .header__heading2 {
            margin: 0;
            line-height: 0;
            letter-spacing: 2.2rem;
            font-weight: 300;
        }
        .header__texto {
            max-width: 60%;
            margin-top: 5rem;
            text-align: center;
            font-size: 2rem;
            font-weight: 700;
            letter-spacing: 2px;
        }
    }
    
}

.header__barra {
    position: relative;
    width: 100%;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
}
@include m.mq(768px) {
    .header__barra {
        position: relative;
        width: 100%;
        z-index: 2;
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
    }
}
.header__logo {
    max-width: 15rem;
    max-height: 15rem;
    margin: 0 auto;
}
@include m.mq(768px) {
    .header__logo {
        max-width: 30rem;
        max-height: 30rem;
    }
}
.header__logoImagen {
    width: 100%;
    height: 100%;
}



